.hacker_rig {
  width: 255px;
}

div#messages {
  margin: 0;
}

div#shelf.container-fluid {
  margin: 0 auto 20px;
  padding-left: 25px;
  padding-right: 25px;
}

.teaser-1.col-md-12 {
  padding-left: 0;
  padding-right: 0;
}

.teaser-1.col-md-12 h3 {
  float: left;
  margin: 0 0 20px;
  padding-right: 10px;
}

ul.nav-inline {
  margin: 0 0 20px;
  display: block;
  float: right;
}
ul.nav-inline li {
  display: inline-block;
  margin: 0;
}

ul.nav-inline li a {
  display: block;
  padding: 0 10px;
  margin: 0 10px 0 0;
  text-align: center;
  background-color: rgba(34, 34, 34, 0.8);
  border-radius: 4px;
}

ul.nav-inline li a:hover {
  background-color: rgba(0, 153, 153, 1);
  text-decoration: none;
}

ul.nav-inline li.active a {
  background-color: rgba(0, 153, 153, 0.6);
}

.moduletable_none {
  background-image: none;
  border: 0 none;
  border-radius: 15px;
}

.custom {
  padding: 35px 0;
}

div#header {
  margin: 0 0 2em;
}

ul.nav-inline li:first-child a {
  padding-left: 30px;
  background-image: url("../images/star.png");
  background-repeat: no-repeat;
  background-position: 7px 2px !important;
}

#sidebar-2 {
  float: left !important;
}

#wrapper.container-fluid {
  padding-left: 10px;
  padding-right: 10px;
}

.moduletable, .moduletable_menu, .moduletable_text {
  overflow: hidden;
}


#BodyContent {
  margin: 0 auto;
  width:1280px;
